$prefix: '.rise-blocks';

#{$prefix}-center {
    text-align: center;
}

#{$prefix}-right {
    text-align: right;
}

$icon-box-size: 6rem;

%common-icon-style {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}

#{$prefix}-icon-box {

    #{$prefix}-icon-wrapper {
        #{$prefix}-icon {
            font-size: 2rem;
            display: inline-block;
            line-height: 0;

            &#{$prefix}-frame-icon-layout {
                @extend %common-icon-style;
                border: 2px solid transparent;
            }

            &#{$prefix}-stack-icon-layout {
                @extend %common-icon-style;
            }
        }
    }

    #{$prefix}-title {
        a {
            text-decoration: none;
            color: inherit;
        }
    }
}



/* =============================
 *  Save Function Style
 * ============================= */
#{$prefix}-icon-box {
    padding: 60px 35px;

    /* Icon Section */
    #{$prefix}-icon-wrapper {
        #{$prefix}-icon {
            font-size: 40px;

            /* Frame icon layout */
            &#{$prefix}-frame-icon-layout {
                border: 2px solid #000000;
            }

            /* Stack icon layout */
            &#{$prefix}-stack-icon-layout {
                color: rgb(255, 255, 255);
                background-color: #000000;
            }

            /* Not stack */
            &:not(#{$prefix}-stack-icon-layout) {
                color: #000000;
            }

            /* Not Initial Icon layout  */
            &:not(#{$prefix}-initial-icon-layout) {
                width: 80px;
                height: 80px;
            }
        }
    }


    /* Content section */
    #{$prefix}-content {
        word-break: break-all;

        #{$prefix}-title {
            color: rgb(0, 0, 0);
            font-weight: 600;
            line-height: 1.6;
            font-size: 24px;
            padding: 30px 0px 15px;
            margin: 0;
        }

        #{$prefix}-description {
            font-weight: 400;
            line-height: 1.5;
            font-size: 15px;
            color: rgb(111, 111, 111);
            margin: 0;
        }
    }


}